    pointer-events: none;
    opacity: 0.3;
  }
  
  .tabs-nav-container {
    position: relative;
  }
  
  .tabs-tab-prev .tabs-tab-prev-icon,
  .tabs-tab-next .tabs-tab-next-icon {
    color: rgba(0, 0, 0, 0.25);
  }
  
  .tabs-nav-wrap {
    overflow: hidden;
  }
