    zoom: 1;
  }
  
  .tabs-nav {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
  }
  
  .tabs-tab {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
  }
  
  .ant-tabs-ink-bar {
    display: none;
  }
  
  .ant-tabs-content {
    display: none;
  }
  
  .ant-tabs-tabpane {
